.First_content {
        /* border: 1px solid rgb(199, 0, 0); */
        width: 100%;
        flex-grow: 1;
        display: none;
        /* display: flex; */
        flex-direction: column;
        align-items: center;
        position: relative;
}

.container_first_slider {
    /* border: 1px solid black; */
    width: 100%;
    flex-grow: 1;

    position:absolute;

    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.first_item_slide_left {
    background: url("../../../../../../../../public/img/davey-heuser-6w9ARb2p2fc-unsplash.jpg");

}

.first_item_slide_right {
    background: url("../../../../../../../../public/img/hannah-gullixson-0IEemURq3GM-unsplash.jpg");
}

.first_item_slide_center {
    background: url("../../../../../../../../public/img/steffan-mitchell-gHrgeeCCkYA-unsplash.jpg");
}

.first_item_slide_left , .first_item_slide_right {
    /* border: 1px solid black; */
    border-radius: 15px;
    width: 240px;
    height: 300px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}


.first_item_slide_center {
    /* border: 1px solid black; */
    border-radius: 15px;
    width: 340px;
    height: 400px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.container_main_title {
    /* border: 1px solid rgb(0, 60, 255); */
    width: 100%;
    height: 175px;

    position:absolute;
    bottom: -58px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.first_title_in_main {
    /* border: 1px solid black; */
    width: 460px;
    height:80px;
    text-align: center;
    line-height: 68px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    color: #bba78c;
    font-size: 6.5rem;
    text-shadow: -3px 2px 20px black;
}

.first_text_in_main {
    /* border: 1px solid black; */
    width: 700px;
    text-align: center;
    color: #bba78c;
    text-shadow: -3px 2px 20px black;
}

.first_btn_in_main {
    /* border: 1px solid black; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.first_btn_in_main > button {

    background-color: #593200;
    color: #bba78c;
    border: none;
    width: 200px;
    height: 40px;
    font-size: 20px;
    box-shadow:-2px 2px 20px 2px #00000059;

}

@media (min-width:880px) {
    .First_content {
        display: flex;
    }
}